import { KEYBOARD_EVENTS } from "./constants";

export const copyObject = (item) => {
  return JSON.parse(JSON.stringify(item));
};

export function getProp(object = {}, keys, defaultVal, ignoreNulls = true) {
  keys = Array.isArray(keys) ? keys : keys.split(".");
  object = object?.[keys[0]];
  if (object && keys.length > 1) {
    return getProp(object, keys.slice(1));
  }
  if (object === null && !ignoreNulls) return defaultVal;
  return object === undefined ? defaultVal : object;
}

export function keyHandler(fn, ...arg) {
  let isEventFired = false;
  return {
    click: (event) => {
      if (!isEventFired) return fn(...arg, event);
      if (isEventFired) isEventFired = false;
    },
    keydown: (event) => {
      const key = event.which || event.keyCode;
      if (KEYBOARD_EVENTS.ENTER.includes(key)) {
        isEventFired = true;
        return fn(...arg, event);
      }
      return true;
    }
  };
}
